.footer {
  max-height: 30vh;
  width: 100%;
  background-color: var(--dark);
  box-shadow: inset 0 1rem 0.5rem -0.3rem rgba(0, 0, 0, 0.141);
}

.footer-row {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-bottom: .5rem;
}

.footer-column {
  width: 90%;
  margin: 5%;
}

.footer-column > h1 {
  font-size: larger;
  color: var(--primary-light);
}

.footer > p {
  margin-left: 4rem;
  color: var(--primary-light);
}

.footer-column > p {
  color: var(--secondary-dark);
}

.footer-bold {
  font-weight: bold;
  font-size: 1rem;
  transition: 300ms ease-in-out color;
}

.footer-bold:hover {
  color: var(--secondary);
}

.copyright {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 4vh;
  color: grey;
  background: var(--dark-light);
}

.copyright > p {
  margin-left: 0.25rem;
}

@media screen and (min-width: 30em ) {
  .footer-row {
    display: flex;
    flex-direction: row;
    align-items: baseline;
  }
}

@media screen and (min-width: 62em) {
  .footer {
    height: 30vh;
    width: 100%;
    background-color: var(--dark);
    box-shadow: inset 0 1rem 0.5rem -0.3rem rgba(0, 0, 0, 0.141);
  }

  .footer > p {
    margin-left: 4rem;
    color: var(--primary-light);
  }

  .footer-row {
    display: flex;
    flex-direction: row;
  }

  .footer-column {
    width: 38vw;
    margin: 5vw;
  }

  .footer-column > h1 {
    font-size: xx-large;
    color: var(--primary-light);
  }

  .footer-column > p {
    color: var(--secondary-dark);
  }

  .footer-bold {
    font-weight: bold;
    font-size: 1rem;
    transition: 300ms ease-in-out color;
  }

  .footer-bold:hover {
    color: var(--secondary);
  }

  .copyright {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 4vh;
    color: grey;
    background: var(--dark-light);
  }

  .copyright > p {
    margin-left: 0.25rem;
  }
}
